 *   
 */

/*   */
.filter-sidebar {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.filter-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 15px;
  background-color: #f9f9f9;
  border-bottom: 1px solid var(--border-color);
}

.filter-header h4 {
  margin: 0;
  font-size: 0.9rem;
  font-weight: 500;
}

.filter-controls {
  display: flex;
  gap: 5px;
}

/*     */
.filter-controls .btn,
.filter-header .btn {
  padding: 0.25rem 0.5rem;
  font-size: 0.8rem;
  width: 28px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.filter-controls .btn i,
.filter-header .btn i {
  font-size: 0.85rem;
}

.filters-container {
  flex: 1;
  overflow-y: auto;
  padding: 8px;
}

/*   */
.filter-card {
  background-color: #fff;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  margin-bottom: 12px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  transition: max-height 0.3s ease-out, opacity 0.3s ease-out;
  overflow: hidden;
}

.filter-card:last-child {
  margin-bottom: 0;
}

.filter-card.expanded .filter-header {
  background-color: #ffffff;
}

.filter-card.collapsed .filter-body {
  max-height: 0;
  padding-top: 0;
  padding-bottom: 0;
  opacity: 0;
  overflow: hidden;
}

.filter-card .filter-header {
  cursor: pointer;
  background-color: #f8f9fa;
  padding: 8px 10px;
}

.filter-title {
  margin: 0;
  display: flex;
  align-items: center;
  font-size: 0.85rem;
  cursor: pointer;
  flex-grow: 1;
  flex-shrink: 1;
  min-width: 0;
  overflow: hidden;
}

.filter-name-wrapper {
  display: flex;
  align-items: center;
  flex-grow: 1;
  min-width: 0;
  overflow: hidden;
}

.filter-name {
  margin-right: 8px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  flex-shrink: 1;
  max-width: 180px;
  font-size: 0.85rem;
}

.filter-type {
  font-size: 0.75rem;
  padding: 0.25rem 0.5rem;
  flex-shrink: 0;
}

.filter-body {
  padding: 10px;
  transition: max-height 0.3s ease-out, padding 0.3s ease-out, opacity 0.3s ease-out;
  max-height: 500px;
  opacity: 1;
}

/*   */
.filter-options {
  margin-top: 8px;
}

/*     (  ) */
.filter-options.numeric-options,
.filter-options.text-options,
.filter-options.date-options {
  display: none;
}

/*     */
.filter-options.active {
  display: block;
}

.value-list {
  max-height: 180px;
  overflow-y: auto;
  background-color: #f9f9f9;
  font-size: 0.8rem;
}

.value-item {
  display: flex;
  align-items: center;
  padding: 6px 10px;
  border-bottom: 1px solid #eee;
}

.value-item:last-child {
  border-bottom: none;
}

.value-checkbox {
  margin-right: 8px;
}

.value-label {
  display: flex;
  align-items: center;
  cursor: pointer;
  user-select: none;
  width: 100%;
  font-size: 0.8rem;
}

.value-count {
  font-size: 0.75rem;
  color: #888;
  margin-left: auto;
}

/*   */
.filter-results-preview {
  padding: 12px;
  border-top: 1px solid var(--border-color);
  background-color: #f5f9ff;
  font-size: 0.85rem;
}

.results-summary h5 {
  margin-top: 0;
  margin-bottom: 8px;
  font-size: 0.9rem;
}

.pnu-preview {
  font-size: 0.8rem;
  margin-top: 8px;
  background-color: #fff;
  padding: 8px;
  border-radius: 4px;
  border: 1px solid #e0e0e0;
}

.pnu-item {
  font-family: monospace;
  background-color: #f0f0f0;
  padding: 2px 4px;
  border-radius: 3px;
  font-size: 0.8rem;
} `, "",{"version":3,"sources":["webpack://./src/styles/components/filter.css"],"names":[],"mappings":"AAAA;;EAEE;;AAEF,YAAY;AACZ;EACE,YAAY;EACZ,aAAa;EACb,sBAAsB;AACxB;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;EACnB,kBAAkB;EAClB,yBAAyB;EACzB,4CAA4C;AAC9C;;AAEA;EACE,SAAS;EACT,iBAAiB;EACjB,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,QAAQ;AACV;;AAEA,oBAAoB;AACpB;;EAEE,uBAAuB;EACvB,iBAAiB;EACjB,WAAW;EACX,YAAY;EACZ,aAAa;EACb,mBAAmB;EACnB,uBAAuB;AACzB;;AAEA;;EAEE,kBAAkB;AACpB;;AAEA;EACE,OAAO;EACP,gBAAgB;EAChB,YAAY;AACd;;AAEA,UAAU;AACV;EACE,sBAAsB;EACtB,qCAAqC;EACrC,kBAAkB;EAClB,mBAAmB;EACnB,yCAAyC;EACzC,2DAA2D;EAC3D,gBAAgB;AAClB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,aAAa;EACb,cAAc;EACd,iBAAiB;EACjB,UAAU;EACV,gBAAgB;AAClB;;AAEA;EACE,eAAe;EACf,yBAAyB;EACzB,iBAAiB;AACnB;;AAEA;EACE,SAAS;EACT,aAAa;EACb,mBAAmB;EACnB,kBAAkB;EAClB,eAAe;EACf,YAAY;EACZ,cAAc;EACd,YAAY;EACZ,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,YAAY;EACZ,YAAY;EACZ,gBAAgB;AAClB;;AAEA;EACE,iBAAiB;EACjB,mBAAmB;EACnB,gBAAgB;EAChB,uBAAuB;EACvB,cAAc;EACd,gBAAgB;EAChB,kBAAkB;AACpB;;AAEA;EACE,kBAAkB;EAClB,uBAAuB;EACvB,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,kFAAkF;EAClF,iBAAiB;EACjB,UAAU;AACZ;;AAEA,UAAU;AACV;EACE,eAAe;AACjB;;AAEA,iCAAiC;AACjC;;;EAGE,aAAa;AACf;;AAEA,kBAAkB;AAClB;EACE,cAAc;AAChB;;AAEA;EACE,iBAAiB;EACjB,gBAAgB;EAChB,yBAAyB;EACzB,iBAAiB;AACnB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,iBAAiB;EACjB,6BAA6B;AAC/B;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,eAAe;EACf,iBAAiB;EACjB,WAAW;EACX,iBAAiB;AACnB;;AAEA;EACE,kBAAkB;EAClB,WAAW;EACX,iBAAiB;AACnB;;AAEA,YAAY;AACZ;EACE,aAAa;EACb,yCAAyC;EACzC,yBAAyB;EACzB,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,kBAAkB;EAClB,iBAAiB;AACnB;;AAEA;EACE,iBAAiB;EACjB,eAAe;EACf,sBAAsB;EACtB,YAAY;EACZ,kBAAkB;EAClB,yBAAyB;AAC3B;;AAEA;EACE,sBAAsB;EACtB,yBAAyB;EACzB,gBAAgB;EAChB,kBAAkB;EAClB,iBAAiB;AACnB","sourcesContent":["/**\n *   \n */\n\n/*   */\n.filter-sidebar {\n  height: 100%;\n  display: flex;\n  flex-direction: column;\n}\n\n.filter-header {\n  displa