/**
 * OpenLayers    
 *       
 */

export default class OpenLayersLoader {
  /**
   * OpenLayers  CDN URL
   *    6.9.0 
   */
  static OL_CSS_URL = 'https://cdn.jsdelivr.net/gh/openlayers/openlayers.github.io@master/en/v6.9.0/css/ol.css';
  
  // CDN URL  ( )
  static CDN_URLS = [
    'https://cdn.jsdelivr.net/gh/openlayers/openlayers.github.io@master/en/v6.9.0/build/ol.js',
    'https://cdn.jsdelivr.net/npm/ol@6.9.0/dist/ol.js',
    'https://unpkg.com/ol@6.9.0/dist/ol.js',
    'https://cdnjs.cloudflare.com/ajax/libs/ol3/6.9.0/ol.js'
  ];
  
  //   
  static MAX_RETRIES = 5;
  
  //   
  static isLoading = false;
  static loadPromise = null;
  
  /**
   * OpenLayers    
   * @returns {boolean}  
   */
  static isLoaded() {
    return typeof window.ol !== 'undefined';
  }
  
  /**
   * OpenLayers CSS 
   * @param {number} retryCount  
   * @returns {Promise<void>} CSS     Promise
   */
  static loadCSS(retryCount = 0) {
    return new Promise((resolve, reject) => {
      //  CSS   
      if (document.querySelector(`link[href="${this.OL_CSS_URL}"]`)) {
        console.log('OpenLayers CSS   ');
        resolve();
        return;
      }
      
      console.log(`OpenLayers CSS   ( ${retryCount + 1}/${this.MAX_RETRIES + 1}):`, this.OL_CSS_URL);
      
      // CSS 
      const link = document.createElement('link');
      link.rel = 'stylesheet';
      link.href = this.OL_CSS_URL;
      link.onload = () => {
        console.log('OpenLayers CSS  ');
        resolve();
      };
      link.onerror = (error) => {
        console.error(`OpenLayers CSS   ( ${retryCount + 1}/${this.MAX_RETRIES + 1}):`, error);
        
        //     
        if (retryCount < this.MAX_RETRIES) {
          console.log(`OpenLayers CSS   (${retryCount + 2}/${this.MAX_RETRIES + 1})...`);
          setTimeout(() => {
            this.loadCSS(retryCount + 1)
              .then(resolve)
              .catch(reject);
          }, 1000); // 1  
        } else {
          reject(new Error('OpenLayers CSS  :    '));
        }
      };
      
      document.head.appendChild(link);
    });
  }
  
  /**
   * OpenLayers JS  -   
   * @returns {Promise<void>} JS     Promise
   */
  static loadJS(retryCount = 0) {
    return new Promise((resolve, reject) => {
      //   
      if (this.isLoaded()) {
        console.log('OpenLayers JS   ');
        resolve();
        return;
      }
      
      //   URL 
      const currentUrl = this.CDN_URLS[retryCount % this.CDN_URLS.length];
      
      console.log(`OpenLayers JS   (${retryCount + 1}/${this.MAX_RETRIES}): ${currentUrl}`);
      
      //  
      const script = document.createElement('script');
      script.src = currentUrl;
      script.async = false; //   
      script.crossOrigin = 'anonymous';
      
      script.onload = () => {
        console.log('OpenLayers   , window.ol  ...');
        
        // ol  
        setTimeout(() => {
          if (window.ol) {
            console.log('window.ol !');
            resolve();
          } else {
            console.warn('  window.ol  ');
            
            if (retryCount < this.MAX_RETRIES - 1) {
              //  
              script.parentNode.removeChild(script);
              //  URL 
              this.loadJS(retryCount + 1)
                .then(resolve)
                .catch(reject);
            } else {
              reject(new Error('OpenLayers    '));
            }
          }
        }, 500);
      };
      
      script.onerror = (error) => {
        console.error(`   (${retryCount + 1}/${this.MAX_RETRIES}):`, error);
        
        if (retryCount < this.MAX_RETRIES - 1) {
          //  
          if (script.parentNode) {
            script.parentNode.removeChild(script);
          }
          //  URL 
          this.loadJS(retryCount + 1)
            .then(resolve)
            .catch(reject);
        } else {
          reject(new Error(' CDN OpenLayers  '));
        }
      };
      
      document.body.appendChild(script);
    });
  }
  
  /**
   * OpenLayers    (CSS + JS)
   * @returns {Promise<void>}     Promise
   */
  static async load() {
    //      Promise 
    if (this.isLoaded()) {
      console.log('OpenLayers    ');
      return Promise.resolve();
    }
    
    if (this.isLoading && this.loadPromise) {
      console.log('OpenLayers    ');
      return this.loadPromise;
    }
    
    //   
    this.isLoading = true;
    
    //  Promise   
    this.loadPromise = (async () => {
      try {
        console.log('OpenLayers   ');
        
        // CSS JS  
        await this.loadCSS();
        await this.loadJS();
        
        //  
        if (!window.ol) {
          throw new Error('OpenLayers    ');
        }
        
        console.log('OpenLayers   !');
        return true;
      } catch (error) {
        console.error('OpenLayers   :', error);
        this.isLoading = false;
        throw error;
      } finally {
        this.isLoading = false;
      }
    })();
    
    return this.loadPromise;
  }
} 