/**
 * Leaflet    
 *   Leaflet     .
 */

export default class LeafletLoader {
  /**
   * Leaflet  CDN URL
   */
  static LEAFLET_CSS_URL = 'https://unpkg.com/leaflet@1.9.4/dist/leaflet.css';
  static LEAFLET_JS_URL = 'https://unpkg.com/leaflet@1.9.4/dist/leaflet.js';
  
  /**
   * Leaflet    
   * @returns {boolean}  
   */
  static isLoaded() {
    return typeof window.L !== 'undefined';
  }
  
  /**
   * Leaflet CSS 
   * @returns {Promise<void>} CSS     Promise
   */
  static loadCSS() {
    return new Promise((resolve, reject) => {
      //  CSS   
      if (document.querySelector(`link[href="${this.LEAFLET_CSS_URL}"]`)) {
        resolve();
        return;
      }
      
      // CSS 
      const link = document.createElement('link');
      link.rel = 'stylesheet';
      link.href = this.LEAFLET_CSS_URL;
      link.onload = () => resolve();
      link.onerror = () => reject(new Error('Leaflet CSS  '));
      
      document.head.appendChild(link);
    });
  }
  
  /**
   * Leaflet JS 
   * @returns {Promise<void>} JS     Promise
   */
  static loadJS() {
    return new Promise((resolve, reject) => {
      //  JS   
      if (this.isLoaded() || document.querySelector(`script[src="${this.LEAFLET_JS_URL}"]`)) {
        resolve();
        return;
      }
      
      // JS 
      const script = document.createElement('script');
      script.src = this.LEAFLET_JS_URL;
      script.async = true;
      script.onload = () => resolve();
      script.onerror = () => reject(new Error('Leaflet JS  '));
      
      document.body.appendChild(script);
    });
  }
  
  /**
   * Leaflet    (CSS + JS)
   * @returns {Promise<void>}     Promise
   */
  static async load() {
    if (this.isLoaded()) {
      return;
    }
    
    try {
      // CSS JS  
      await Promise.all([
        this.loadCSS(),
        this.loadJS()
      ]);
      
      console.log('Leaflet   ');
    } catch (error) {
      console.error('Leaflet   :', error);
      throw error;
    }
  }
} 