/**
 * PNU  
 * PNU    .
 */
export default class PnuCoordinateService {
  constructor() {
    this.pnuCoordinateMap = new Map();
    this.isLoaded = false;
    this.isLoading = false;
    
    //    
    this._parseCsvData = this._parseCsvData.bind(this);
  }
  
  /**
   * CSV  PNU-  
   * @param {string} csvUrl PNU- CSV  URL
   * @returns {Promise<void>}
   */
  async loadFromCsv(csvUrl) {
    if (this.isLoaded || this.isLoading) return;
    
    this.isLoading = true;
    
    try {
      console.log('PNU-   :', csvUrl);
      
      const response = await fetch(csvUrl);
      if (!response.ok) {
        throw new Error(`CSV  : ${response.status} ${response.statusText}`);
      }
      
      const csvText = await response.text();
      this._parseCsvData(csvText);
      
      this.isLoaded = true;
      console.log(`PNU-   : ${this.pnuCoordinateMap.size} `);
    } catch (error) {
      console.error('PNU-   :', error);
      throw error;
    } finally {
      this.isLoading = false;
    }
  }
  
  /**
   * CSV  
   * @private
   * @param {string} csvText CSV  
   */
  _parseCsvData(csvText) {
    console.log('CSV  ');
    // CSV    
    const lines = csvText.split('\n');
    
    if (lines.length <= 1) {
      console.warn('CSV    .');
      return;
    }
    
    console.log('CSV  :', lines.length);
    console.log(' ():', lines[0]);
    console.log('  ( ):', lines[1]);
    
    const headers = lines[0].split(',').map(h => h.trim().replace(/"/g, ''));
    console.log(' :', headers);
    
    // PNU , x, y   
    const pnuIndex = headers.findIndex(h => 
      h.toLowerCase().includes('pnu') || h.toLowerCase().includes('code'));
    const xIndex = headers.findIndex(h => h.toLowerCase() === 'x');
    const yIndex = headers.findIndex(h => h.toLowerCase() === 'y');
    
    console.log(' :', { pnuIndex, xIndex, yIndex });
    
    if (pnuIndex === -1 || xIndex === -1 || yIndex === -1) {
      console.error('CSV   :', { pnuIndex, xIndex, yIndex });
      throw new Error('CSV   (PNU, x, y)   .');
    }
    
    //  
    for (let i = 1; i < lines.length; i++) {
      const line = lines[i].trim();
      if (!line) continue;
      
      //   CSV  
      const values = this._parseCSVLine(line);
      
      const pnuCode = values[pnuIndex].replace(/"/g, '');
      const longitude = parseFloat(values[xIndex]); // x  (longitude)
      const latitude = parseFloat(values[yIndex]);  // y  (latitude)
      
      if (pnuCode && !isNaN(longitude) && !isNaN(latitude)) {
        this.pnuCoordinateMap.set(pnuCode, { 
          latitude: latitude, 
          longitude: longitude 
        });
      }
    }
  }
  
  /**
   * CSV   ( )
   * @private
   * @param {string} line CSV 
   * @returns {Array<string>}   
   */
  _parseCSVLine(line) {
    const result = [];
    let current = '';
    let inQuotes = false;
    
    for (let i = 0; i < line.length; i++) {
      const char = line[i];
      
      if (char === '"') {
        inQuotes = !inQuotes;
      } else if (char === ',' && !inQuotes) {
        result.push(current);
        current = '';
      } else {
        current += char;
      }
    }
    
    //   
    result.push(current);
    
    return result;
  }
  
  /**
   * PNU   
   * @param {string} pnuCode PNU 
   * @returns {Object|null}    null
   */
  getCoordinatesByPnu(pnuCode) {
    return this.pnuCoordinateMap.get(pnuCode) || null;
  }
  
  /**
   *  PNU     
   * @param {Array<string>} pnuCodes PNU  
   * @returns {Map<string, Object>} PNU     
   */
  getCoordinatesForPnuList(pnuCodes) {
    const result = new Map();
    
    for (const pnuCode of pnuCodes) {
      const coords = this.getCoordinatesByPnu(pnuCode);
      if (coords) {
        result.set(pnuCode, coords);
      }
    }
    
    return result;
  }
} 