/**
 *    API 
 */

import { API_CONFIG } from '../core/config.js';

export default class IsochroneApiService {
  /**
   *  API  
   */
  constructor() {
    this.baseUrl = API_CONFIG.BASE_URL;
    this.timeout = API_CONFIG.TIMEOUT;
  }

  /**
   * API URL 
   * @param {string} endpoint API 
   * @returns {string}  API URL
   */
  getUrl(endpoint) {
    return `${this.baseUrl}${endpoint}`;
  }

  /**
   * API  
   * @param {string} endpoint API 
   * @param {object} options fetch 
   * @returns {Promise<object>} API 
   */
  async fetchApi(endpoint, options = {}) {
    const url = this.getUrl(endpoint);
    
    const defaultOptions = {
      headers: {
        'Content-Type': 'application/json'
      },
      timeout: this.timeout
    };
    
    const fetchOptions = { ...defaultOptions, ...options };
    
    try {
      console.log(`API : ${url}`, JSON.stringify(fetchOptions.body ? JSON.parse(fetchOptions.body) : {}, null, 2));
      
      const controller = new AbortController();
      const timeoutId = setTimeout(() => controller.abort(), this.timeout);
      
      const response = await fetch(url, {
        ...fetchOptions,
        signal: controller.signal
      });
      
      clearTimeout(timeoutId);
      
      if (!response.ok) {
        const errorText = await response.text();
        throw new Error(`API  (${response.status}): ${response.statusText}\n${errorText}`);
      }
      
      const data = await response.json();
      console.log(`API : ${url}`, data);
      return data;
    } catch (error) {
      console.error(`API   (${url}):`, error);
      
      if (error.name === 'AbortError') {
        throw new Error(`API   : ${this.timeout}ms .`);
      }
      
      throw error;
    }
  }

  /**
   *    
   * @returns {Promise<object>}  
   */
  async getGridTables() {
    return this.fetchApi('/isochrone/grid-tables');
  }

  /**
   *    
   * @param {string} tableName  
   * @returns {Promise<object>}  
   */
  async getTableColumns(tableName) {
    if (!tableName) {
      throw new Error('  .');
    }
    return this.fetchApi(`/isochrone/columns/${encodeURIComponent(tableName)}`);
  }

  /**
   *   
   * @param {object} params   
   * @returns {Promise<object>}  
   */
  async generateIsochrone(params) {
    if (!params || typeof params !== 'object') {
      throw new Error('   .');
    }
    
    const requiredFields = ['pnu', 'rangeMinutes'];
    for (const field of requiredFields) {
      if (!params[field]) {
        throw new Error(`  : ${field}`);
      }
    }
    
    return this.fetchApi('/isochrone/generate', {
      method: 'POST',
      body: JSON.stringify(params)
    });
  }
  
  /**
   *   
   * @param {string} pnu PNU 
   * @param {number} rangeMinutes ()
   * @param {string} gridTable  
   * @param {string} columnName  
   * @returns {Promise<object>}  
   */
  async analyzeIsochrone(pnu, rangeMinutes, gridTable, columnName) {
    //   
    if (!pnu) throw new Error('PNU  .');
    if (!rangeMinutes) throw new Error('() .');
    if (!gridTable) throw new Error('  .');
    if (!columnName) throw new Error('  .');
    
    //    (  )
    const numericRangeMinutes = Number(rangeMinutes);
    
    //   
    if (isNaN(numericRangeMinutes)) {
      throw new Error('()  .');
    }
    
    //   
    const params = {
      pnu,
      rangeMinutes: numericRangeMinutes,
      gridTable,
      columnName
    };
    
    // API 
    const endpoint = '/isochrone/analyze';
    
    console.log(`  : PNU=${pnu}, =${numericRangeMinutes}, =${gridTable}, =${columnName}`);
    
    try {
      const response = await this.fetchApi(endpoint, {
        method: 'POST',
        body: JSON.stringify(params)
      });
      
      //   
      if (!response) {
        throw new Error('  .');
      }
      
      //   
      if (!response.success) {
        console.error('  :', response.message || '   ');
        return {
          success: false,
          message: response.message || '  .'
        };
      }
      
      console.log('  :', response.data?.pnu);
      return response;
    } catch (error) {
      console.error('  API  :', error);
      return {
        success: false,
        message: `API : ${error.message}`,
        error: error
      };
    }
  }
}
