 *  
 */

/*    */
.main-layout {
  display: flex;
  height: 100vh;
  overflow: hidden;
}

.left-sidebar {
  width: var(--sidebar-width);
  border-right: 1px solid var(--border-color);
  overflow-y: auto;
  background-color: var(--background-light);
  transition: width 0.3s ease;
}

.right-sidebar {
  width: var(--sidebar-width);
  border-left: 1px solid var(--border-color);
  overflow-y: auto;
  background-color: var(--background-light);
  transition: width 0.3s ease;
}

.main-content {
  flex: 1;
  overflow: hidden;
  position: relative;
  background-color: #fff;
}

/*   */
@media (max-width: 992px) {
  .main-layout {
    flex-direction: column;
    height: auto;
    min-height: 100vh;
  }
  
  .left-sidebar,
  .right-sidebar {
    width: 100%;
    max-height: 50vh;
    border-right: none;
    border-left: none;
  }
  
  .left-sidebar {
    border-bottom: 1px solid var(--border-color);
  }
  
  .right-sidebar {
    border-top: 1px solid var(--border-color);
  }
  
  .main-content {
    height: 50vh;
  }
}

/*  / */
.sidebar-toggle {
  position: absolute;
  top: 50%;
  width: 20px;
  height: 50px;
  background-color: #fff;
  border: 1px solid var(--border-color);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 100;
  border-radius: 0 4px 4px 0;
}

.left-toggle {
  right: -20px;
  border-left: none;
}

.right-toggle {
  left: -20px;
  border-right: none;
}

.sidebar-collapsed {
  width: 0;
  overflow: hidden;
  border: none;
}

/*     */
.sidebar-collapsed .sidebar-toggle {
  right: -20px;
} `, "",{"version":3,"sources":["webpack://./src/styles/components/layout.css"],"names":[],"mappings":"AAAA;;EAEE;;AAEF,eAAe;AACf;EACE,aAAa;EACb,aAAa;EACb,gBAAgB;AAClB;;AAEA;EACE,2BAA2B;EAC3B,2CAA2C;EAC3C,gBAAgB;EAChB,yCAAyC;EACzC,2BAA2B;AAC7B;;AAEA;EACE,2BAA2B;EAC3B,0CAA0C;EAC1C,gBAAgB;EAChB,yCAAyC;EACzC,2BAA2B;AAC7B;;AAEA;EACE,OAAO;EACP,gBAAgB;EAChB,kBAAkB;EAClB,sBAAsB;AACxB;;AAEA,aAAa;AACb;EACE;IACE,sBAAsB;IACtB,YAAY;IACZ,iBAAiB;EACnB;;EAEA;;IAEE,WAAW;IACX,gBAAgB;IAChB,kBAAkB;IAClB,iBAAiB;EACnB;;EAEA;IACE,4CAA4C;EAC9C;;EAEA;IACE,yCAAyC;EAC3C;;EAEA;IACE,YAAY;EACd;AACF;;AAEA,gBAAgB;AAChB;EACE,kBAAkB;EAClB,QAAQ;EACR,WAAW;EACX,YAAY;EACZ,sBAAsB;EACtB,qCAAqC;EACrC,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,eAAe;EACf,YAAY;EACZ,0BAA0B;AAC5B;;AAEA;EACE,YAAY;EACZ,iBAAiB;AACnB;;AAEA;EACE,WAAW;EACX,kBAAkB;AACpB;;AAEA;EACE,QAAQ;EACR,gBAAgB;EAChB,YAAY;AACd;;AAEA,mBAAmB;AACnB;EACE,YAAY;AACd","sourcesContent":["/**\n *  \n */\n\n/*    */\n.main-layout {\n  display: flex;\n  height: 100vh;\n  overflow: hidden;\n}\n\n.left-sidebar {\n  width: var(--sidebar-width);\n  border-right: 1px solid var(--border-color);\n  overflow-y: auto;\n  background-color: var(--background-light);\n  transition: width 0.3s ease;\n}\n\n.right-sidebar {\n  width: var(--sidebar-width);\n  border-left: 1px solid var(--border-color);\n  overflow-y: auto;\n  background-color: var(--background-light);\n  transition: width 0.3s ease;\n}\n\n.main-content {\n  flex: 1;\n  overflow: hidden;\n  position: relative;\n  background-color: #fff;\n}\n\n/*   */\n@media (max-width: 992px) {\n  .main-layout {\n    flex-direction: column;\n    height: auto;\n    min-height: 100vh;\n  }\n  \n  .left-sidebar,\n  .right-sidebar {\n    width: 100%;\n    max-height: 50vh;\n    border-right: none;\n    border-left: none;\n  }\n  \n  .left-sidebar {\n    border-bottom: 1px solid var(--border-color);\n  }\n  \n  .right-sidebar {\n    border-top: 1px solid var(--border-color);\n  }\n  \n  .main-content {\n    height: 50vh;\n  }\n}\n\n/*  