 *   
 */

.grid-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

.grid-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 15px;
  background-color: #f9f9f9;
  border-bottom: 1px solid var(--border-color);
}

.grid-header h4 {
  margin: 0;
  font-size: 0.9rem;
  font-weight: 500;
}

.grid-controls {
  display: flex;
  gap: 5px;
}

.data-grid {
  flex: 1;
  overflow-y: auto;
  background-color: #fff;
}

.grid-placeholder {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  text-align: center;
  background-color: #f9f9f9;
  padding: 20px;
  color: #666;
  font-size: 0.85rem;
}

.grid-pagination {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 15px;
  border-top: 1px solid var(--border-color);
  background-color: #f9f9f9;
}

.grid-info {
  font-size: 0.8rem;
  color: #666;
}

.pagination-controls {
  display: flex;
  align-items: center;
  gap: 10px;
}

.page-info {
  font-size: 0.8rem;
  display: inline-block;
  min-width: 40px;
  text-align: center;
}

/*    */
.location-analysis-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 15px;
  background-color: #f9f9f9;
  border-bottom: 1px solid var(--border-color);
}

.location-analysis-header h4 {
  margin: 0;
  font-size: 0.9rem;
  font-weight: 500;
}

.location-analysis-content {
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow: hidden;
}

/*     */
.index-settings-container {
  background-color: #fff;
  border-bottom: 1px solid var(--border-color);
  padding: 10px;
}

.index-settings-form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.settings-row {
  display: flex;
  align-items: flex-end;
  gap: 10px;
}

.table-column-row {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
}

.setting-item {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.setting-item label {
  font-size: 0.75rem;
  font-weight: 500;
  color: #555;
}

.flex-grow {
  flex: 1;
  min-width: 120px;
}

/*    */
.time-range-group {
  margin-bottom: 5px;
}

.time-range-selector {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
}

.time-option {
  padding: 3px 8px;
  background-color: #f1f1f1;
  border: 1px solid #ddd;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.7rem;
  min-width: 36px;
  text-align: center;
  transition: all 0.2s;
}

.time-option:hover {
  background-color: #e9e9e9;
}

.time-option.active {
  background-color: #007bff;
  color: white;
  border-color: #0069d9;
}

.add-index-btn-container {
  display: flex;
  align-items: flex-end;
}

.add-index-btn {
  width: 28px;
  height: 28px;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.index-settings-list {
  margin-top: 10px;
  max-height: 120px;
  overflow-y: auto;
}

.empty-settings-message {
  text-align: center;
  color: #888;
  font-size: 0.8rem;
  padding: 8px;
}

.index-setting-item {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 6px 8px;
  background-color: #f8f9fa;
  border-radius: 4px;
  margin-bottom: 6px;
}

.setting-index-name {
  padding: 3px 6px;
  background-color: #0d6efd;
  color: white;
  border-radius: 4px;
  font-size: 0.7rem;
  font-weight: 500;
  cursor: pointer;
  white-space: nowrap;
}

.setting-index-name:hover {
  background-color: #0b5ed7;
}

.setting-badges {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
  flex: 1;
  overflow: hidden;
}

.setting-badge {
  background-color: #e9ecef;
  color: #495057;
  padding: 2px 6px;
  border-radius: 4px;
  font-size: 0.65rem;
  white-space: nowrap;
}

.remove-setting-btn {
  margin-left: auto;
  padding: 2px 5px;
  font-size: 0.7rem;
  margin-left: 0;
  flex-shrink: 0;
}

/*    */
.analysis-results-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  background-color: #fff;
  padding: 10px;
}

.pnu-toggle-list {
  border: 1px solid #eee;
  border-radius: 4px;
  overflow: hidden;
  margin-bottom: 15px;
}

.pnu-toggle-header {
  padding: 8px 12px;
  background-color: #f8f9fa;
  border-bottom: 1px solid #eee;
  font-size: 0.8rem;
  font-weight: 500;
}

.pnu-header-count {
  color: #666;
  font-weight: normal;
  margin-left: 5px;
}

.pnu-toggle-body {
  max-height: 150px;
  overflow-y: auto;
}

.empty-pnu-message {
  text-align: center;
  color: #888;
  font-size: 0.8rem;
  padding: 15px;
}

.pnu-toggle-item {
  display: flex;
  align-items: center;
  padding: 6px 12px;
  border-bottom: 1px solid #f0f0f0;
}

.pnu-toggle-item.failed {
  background-color: #fff5f5;
}

.pnu-toggle-item:last-child {
  border-bottom: none;
}

.toggle-switch {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 18px;
  margin-right: 10px;
}

.toggle-input {
  opacity: 0;
  width: 0;
  height: 0;
}

.toggle-label {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: .4s;
  border-radius: 18px;
}

.toggle-label:before {
  position: absolute;
  content: "";
  height: 14px;
  width: 14px;
  left: 2px;
  bottom: 2px;
  background-color: white;
  transition: .4s;
  border-radius: 50%;
}

.toggle-input:checked + .toggle-label {
  background-color: #2196F3;
}

.toggle-input:checked + .toggle-label:before {
  transform: translateX(12px);
}

.pnu-code {
  flex: 1;
  font-size: 0.75rem;
  color: #333;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.pnu-rank {
  padding: 2px 5px;
  border-radius: 4px;
  font-size: 0.65rem;
  font-weight: 600;
  min-width: 30px;
  text-align: center;
  margin-left: auto;
  background-color: #f0f0f0;
  color: #333;
}

.rank-1 {
  background-color: #ffd700;
  color: #333;
  font-weight: 700;
  box-shadow: 0 1px 2px rgba(0,0,0,0.1);
}

.rank-2 {
  background-color: #c0c0c0;
  color: #333;
  font-weight: 700;
  box-shadow: 0 1px 2px rgba(0,0,0,0.1);
}

.rank-3 {
  background-color: #cd7f32;
  color: #fff;
  font-weight: 700;
  box-shadow: 0 1px 2px rgba(0,0,0,0.1);
}

/*     */
.statistics-toggle {
  display: flex;
  justify-content: center;
  margin: 10px 0;
}

.statistics-toggle button {
  width: 100%;
  max-width: 180px;
  font-size: 0.75rem;
}

.result-comparison {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  margin-bottom: 10px;
}

.result-comparison h5 {
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 0.8rem;
  font-weight: 500;
}

.comparison-chart {
  flex: 1;
  border: 1px solid #eee;
  border-radius: 4px;
  overflow: hidden;
}

.placeholder-chart {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  color: #888;
  font-size: 0.8rem;
  background-color: #f9f9f9;
}

.analysis-stats {
  margin: 10px 0;
  padding: 10px;
  background-color: #f8f9fa;
  border-radius: 4px;
  border: 1px solid #eee;
}

.analysis-stats h5 {
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 0.8rem;
  font-weight: 500;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: 10px;
}

.stat-item {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.stat-label {
  font-size: 0.7rem;
  color: #666;
}

.stat-value {
  font-size: 0.8rem;
  font-weight: 500;
  color: #333;
}

.result-actions {
  display: flex;
  justify-content: flex-end;
  gap: 5px;
  margin-top: 10px;
}

/*     */
.ranking-actions {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  margin: 10px 0;
  padding: 8px 10px;
  background-color: #f0f7ff;
  border-radius: 4px;
  border: 1px solid #cfe2ff;
}

.calculate-ranking-btn {
  font-size: 0.85rem;
  padding: 6px 12px;
  white-space: nowrap;
}

.ranking-status {
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 0.8rem;
  color: #0d6efd;
}

.ranking-status .fa-check-circle {
  color: #198754;
}

/* PNU    */
.pnu-toggle-list {
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid var(--border-color);
  overflow: hidden;
  margin-bottom: 10px;
}

.pnu-analyzed-badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 16px;
  height: 16px;
  background: #ecffec;
  border-radius: 50%;
  color: #28a745;
  font-size: 0.6rem;
  margin-left: 5px;
}

.pnu-failed-badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 16px;
  height: 16px;
  background: #ffe9e9;
  border-radius: 50%;
  color: #dc3545;
  font-size: 0.6rem;
  margin-left: 5px;
  cursor: help;
}

/* PNU   */
.pnu-score {
  margin-left: 8px;
  font-size: 0.65rem;
  color: #0066cc;
  font-weight: 500;
  background-color: #e6f2ff;
  border-radius: 4px;
  padding: 2px 6px;
  display: inline-block;
