ax-height: 120px;\n  overflow-y: auto;\n}\n\n.empty-settings-message {\n  text-align: center;\n  color: #888;\n  font-size: 0.8rem;\n  padding: 8px;\n}\n\n.index-setting-item {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  padding: 6px 8px;\n  background-color: #f8f9fa;\n  border-radius: 4px;\n  margin-bottom: 6px;\n}\n\n.setting-index-name {\n  padding: 3px 6px;\n  background-color: #0d6efd;\n  color: white;\n  border-radius: 4px;\n  font-size: 0.7rem;\n  font-weight: 500;\n  cursor: pointer;\n  white-space: nowrap;\n}\n\n.setting-index-name:hover {\n  background-color: #0b5ed7;\n}\n\n.setting-badges {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 4px;\n  flex: 1;\n  overflow: hidden;\n}\n\n.setting-badge {\n  background-color: #e9ecef;\n  color: #495057;\n  padding: 2px 6px;\n  border-radius: 4px;\n  font-size: 0.65rem;\n  white-space: nowrap;\n}\n\n.remove-setting-btn {\n  margin-left: auto;\n  padding: 2px 5px;\n  font-size: 0.7rem;\n  margin-left: 0;\n  flex-shrink: 0;\n}\n\n/*    */\n.analysis-results-container {\n  flex: 1;\n  display: flex;\n  flex-direction: column;\n  overflow: hidden;\n  background-color: #fff;\n  padding: 10px;\n}\n\n.pnu-toggle-list {\n  border: 1px solid #eee;\n  border-radius: 4px;\n  overflow: hidden;\n  margin-bottom: 15px;\n}\n\n.pnu-toggle-header {\n  padding: 8px 12px;\n  background-color: #f8f9fa;\n  border-bottom: 1px solid #eee;\n  font-size: 0.8rem;\n  font-weight: 500;\n}\n\n.pnu-header-count {\n  color: #666;\n  font-weight: normal;\n  margin-left: 5px;\n}\n\n.pnu-toggle-body {\n  max-height: 150px;\n  overflow-y: auto;\n}\n\n.empty-pnu-message {\n  text-align: center;\n  color: #888;\n  font-size: 0.8rem;\n  padding: 15px;\n}\n\n.pnu-toggle-item {\n  display: flex;\n  align-items: center;\n  padding: 6px 12px;\n  border-bottom: 1px solid #f0f0f0;\n}\n\n.pnu-toggle-item.failed {\n  background-color: #fff5f5;\n}\n\n.pnu-toggle-item:last-child {\n  border-bottom: none;\n}\n\n.toggle-switch {\n  position: relative;