-chart {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  height: 100%;\n  color: #888;\n  font-size: 0.8rem;\n  background-color: #f9f9f9;\n}\n\n.analysis-stats {\n  margin: 10px 0;\n  padding: 10px;\n  background-color: #f8f9fa;\n  border-radius: 4px;\n  border: 1px solid #eee;\n}\n\n.analysis-stats h5 {\n  margin-top: 0;\n  margin-bottom: 10px;\n  font-size: 0.8rem;\n  font-weight: 500;\n}\n\n.stats-grid {\n  display: grid;\n  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));\n  gap: 10px;\n}\n\n.stat-item {\n  display: flex;\n  flex-direction: column;\n  gap: 2px;\n}\n\n.stat-label {\n  font-size: 0.7rem;\n  color: #666;\n}\n\n.stat-value {\n  font-size: 0.8rem;\n  font-weight: 500;\n  color: #333;\n}\n\n.result-actions {\n  display: flex;\n  justify-content: flex-end;\n  gap: 5px;\n  margin-top: 10px;\n}\n\n/*     */\n.ranking-actions {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  gap: 10px;\n  margin: 10px 0;\n  padding: 8px 10px;\n  background-color: #f0f7ff;\n  border-radius: 4px;\n  border: 1px solid #cfe2ff;\n}\n\n.calculate-ranking-btn {\n  font-size: 0.85rem;\n  padding: 6px 12px;\n  white-space: nowrap;\n}\n\n.ranking-status {\n  display: flex;\n  align-items: center;\n  gap: 5px;\n  font-size: 0.8rem;\n  color: #0d6efd;\n}\n\n.ranking-status .fa-check-circle {\n  color: #198754;\n}\n\n/* PNU    */\n.pnu-toggle-list {\n  background-color: #fff;\n  border-radius: 4px;\n  border: 1px solid var(--border-color);\n  overflow: hidden;\n  margin-bottom: 10px;\n}\n\n.pnu-analyzed-badge {\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  width: 16px;\n  height: 16px;\n  background: #ecffec;\n  border-radius: 50%;\n  color: #28a745;\n  font-size: 0.6rem;\n  margin-left: 5px;\n}\n\n.pnu-failed-badge {\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  width: 16px;\n  height: 16px;\n  background: #ffe9e9;\n  border-radius: 50%;\n  color: